.search {

    padding: 1%;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,.2), 0 3px 10px 0 rgba(0,0,0,.19);
    box-sizing: border-box;

    .btn {
        margin-top: 25px;
        margin-left: 15px;
    }


}

.dropdown {
    position: relative;
    display: inline-block;
    font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
    font-size: 14px;
    z-index: 5;
}

.dropdown > a, .dropdown > button {
    font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
    font-size: 14px;
    background-color: white;
    border: 1px solid #ccc;
    padding: 6px 20px 6px 10px;
    border-radius: 4px;
    display: inline-block;
    color: black;
    text-decoration: none;
    width: 150px;
    height: 31px;
}

.dropdown > a:before, .dropdown > button:before {
    position: absolute;
    right: 7px;
    top: 12px;
    content: ' ';
    border-left: 5px solid transparent;
    border-right: 5px solid transparent;
    border-top: 5px solid black;
}

.dropdown input[type=checkbox] {
    position: absolute;
    display: block;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    margin: 0px;
    opacity: 0;
}

.dropdown input[type=checkbox]:checked {
    position: fixed;
    z-index:+0;
    top: 0px; left: 0px; 
    right: 0px; bottom: 0px;
}

.dropdown ul {
    position: absolute;
    top: 18px;
    border: 1px solid #ccc;
    border-radius: 3px;
    left: 0px;
    list-style: none;
    padding: 4px 0px;
    display: none;
    background-color: white;
    box-shadow: 0 3px 6px rgba(0,0,0,.175);
    max-height: 400px;
    overflow-y: scroll;
}

.dropdown input[type=checkbox]:checked + ul {
    display: block;
}

.dropdown ul li {
    display: block;
    padding: 6px 20px;
    white-space: nowrap;
    min-width: 150px;
}

.dropdown ul li:hover {
    background-color: #F5F5F5;
    cursor: pointer;
}

.dropdown ul li a {
    text-decoration: none;
    display: block;
    color: black
}

.dropdown .divider {
    height: 1px;
    margin: 9px 0;
    overflow: hidden;
    background-color: #e5e5e5;
    font-size: 1px;
    padding: 0;
}
